@media only screen and (min-width: 415px) {

    #headerwrap {
        background: url(../../assets/img/sunlightphoto.JPG) no-repeat center top;
        margin-top: 100px;
        padding-top:20px;
        text-align:center;
        background-attachment: relative;
        background-position: center center;
        min-height: 500px;
        width: 100%;
        
        -webkit-background-size: 100%;
        -moz-background-size: 100%;
        -o-background-size: 100%;
        background-size: 100%;
    
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
    }
    
    #headerwrap h1 {
        margin-top: 100px;
        color: #1a1b1d;
        font-size: 70px;
        font-weight: 300;
        letter-spacing: 3px;
    }
    
    #headerwrap h2 {
        color: white;
        font-size: 40px;
        font-weight: 700;
        letter-spacing: 2px;
        text-transform: uppercase;
    }

