@media only screen and (max-width: 414px) {

    #headerwrap {
        background: url(../../assets/img/sunlightphoto.JPG) no-repeat center top;
        margin-top: 100px;
        padding-top:20px;
        text-align:center;
        background-attachment: relative;
        background-position: center center;
        min-height: 400px;
        width: 100%;
        
        -webkit-background-size: 100%;
        -moz-background-size: 100%;
        -o-background-size: 100%;
        background-size: 100%;
    
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
    }
    
    #headerwrap h1 {
        margin-top: 100px;
        color: #1a1b1d;
        font-size: 35px;
        font-weight: 300;
        letter-spacing: 3px;
    }
    
    #headerwrap h5 {
        color: #1a1b1d;
        font-size: 14px;
        font-weight: 300;
        letter-spacing: 2px;
    }
    #headerwrap h2 {
        color: white;
        font-size: 30px;
        font-weight: 700;
        letter-spacing: 2px;
        text-transform: uppercase;
    }

